import { useState } from 'react';
import Head from 'next/head';
import Image from 'next/image';
import 'bootstrap/dist/css/bootstrap.min.css';

export default function CartPage() {
  const [cartItems, setCartItems] = useState([
    {
      id: 1,
      name: 'Fifa 19',
      platform: 'PS4',
      price: 44.00,
      quantity: 2,
      image: '/images/fifa.jpg'
    },
    {
      id: 2,
      name: 'Glacier White 500GB',
      platform: 'PS4',
      price: 249.99,
      quantity: 1,
      image: '/images/whiteps4.jpg'
    },
    {
      id: 3,
      name: 'Platinum Headset',
      platform: 'PS4',
      price: 119.99,
      quantity: 1,
      image: '/images/headset.jpg'
    }
  ]);

  const [promoCode, setPromoCode] = useState('');
  const shippingCost = 5;

  const updateQuantity = (id, delta) => {
    setCartItems(prev =>
      prev.map(item =>
        item.id === id
          ? { ...item, quantity: Math.max(1, item.quantity + delta) }
          : item
      )
    );
  };

  const removeItem = (id) => {
    setCartItems(prev => prev.filter(item => item.id !== id));
  };

  const subtotal = cartItems.reduce((sum, item) => sum + item.price * item.quantity, 0);
  const total = subtotal + shippingCost;

  return (
    <>
      <Head>
        <title>Shopping Cart | Game Store</title>
        <meta name="description" content="Review and manage your shopping cart before checkout." />
        <meta name="keywords" content="shopping cart, game cart, checkout" />
        <meta name="robots" content="index, follow" />
        <link rel="canonical" href="https://yourdomain.com/cart" />
      </Head>

      <div className="container py-5">
        <div className="row">
          {/* Cart Section */}
          <div className="col-lg-8 mb-5 mb-lg-0">
            <h3 className="fw-bold mb-4">Shopping Cart</h3>
            <div className="d-flex justify-content-between mb-3">
              <span className="text-muted">PRODUCT DETAILS</span>
              <span className="text-muted">3 Items</span>
            </div>
            <hr />

            {cartItems.map(item => (
              <div className="row align-items-center mb-4" key={item.id}>
                <div className="col-3">
                  <Image src={item.image} alt={item.name} width={80} height={80} className="img-fluid" />
                </div>
                <div className="col-9 col-md-3">
                  <div className="fw-semibold">{item.name}</div>
                  <div className="text-danger small">{item.platform}</div>
                  <div>
                    <button className="btn btn-link p-0 text-danger small" onClick={() => removeItem(item.id)}>Remove</button>
                  </div>
                </div>
                <div className="col-12 col-md-2 d-flex align-items-center mt-3 mt-md-0">
                  <button className="btn btn-outline-dark btn-sm" onClick={() => updateQuantity(item.id, -1)}>-</button>
                  <input
                    type="text"
                    value={item.quantity}
                    className="form-control text-center mx-2"
                    style={{ width: '50px' }}
                    readOnly
                  />
                  <button className="btn btn-outline-dark btn-sm" onClick={() => updateQuantity(item.id, 1)}>+</button>
                </div>
                <div className="col-6 col-md-2 mt-3 mt-md-0">
                  <span>£{item.price.toFixed(2)}</span>
                </div>
                <div className="col-6 col-md-2 mt-3 mt-md-0 text-end">
                  <span>£{(item.price * item.quantity).toFixed(2)}</span>
                </div>
              </div>
            ))}

            <div className="mt-4">
              <a href="/" className="text-decoration-none text-primary fw-bold">
                ← Continue Shopping
              </a>
            </div>
          </div>

          {/* Summary Section */}
          <div className="col-lg-4">
            <h4 className="fw-bold mb-3">Order Summary</h4>
            <div className="d-flex justify-content-between">
              <span>ITEMS</span>
              <span>£{subtotal.toFixed(2)}</span>
            </div>
            <hr />
            <div className="mb-3">
              <label htmlFor="shipping">SHIPPING</label>
              <select id="shipping" className="form-select mt-1">
                <option>Standard Delivery - £{shippingCost.toFixed(2)}</option>
              </select>
            </div>
            <div className="mb-3">
              <label htmlFor="promo">PROMO CODE</label>
              <input
                type="text"
                id="promo"
                className="form-control mt-1"
                placeholder="Enter your code"
                value={promoCode}
                onChange={(e) => setPromoCode(e.target.value)}
              />
              <button className="btn btn-danger w-100 mt-2">APPLY</button>
            </div>
            <hr />
            <div className="d-flex justify-content-between fw-bold">
              <span>TOTAL COST</span>
              <span>£{total.toFixed(2)}</span>
            </div>
            <button className="btn btn-primary w-100 mt-4">CHECKOUT</button>
          </div>
        </div>
      </div>
    </>
  );
}
